@import url('https://fonts.googleapis.com/css2?family=Hind:wght@300;400;500;600;700&display=swap');
.App {
  display: flex;
  flex-direction: column;
}

header{
  padding: 0;
  margin: 0;
}

#root {
  background-color: #0A282E;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

#App-header {
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  padding: 0;
  margin: 4% 10% 10% 10%;
  animation: fadeIn 2s;

}

.fade-in {
  opacity: 0; /* Initial state */
  animation: fadeIn 2s forwards; /* Apply the fade-in animation */
}

.fade-in-visible {
  opacity: 1;
}

.App-link {
  color: #61dafb;
}

.highlighted {
  color: #6DF1B3;
}

.text{
  /* color: #f6f3ec; */
  font-family: 'Hind', 'Roboto';
  /* color: #8EB4D3 */
  color: #98ADB1;
}

#navbar{
  display:flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  margin-top: 1%;
  gap: 3%;
  margin-right: 2%;
}

a {
  text-decoration: none;
  /* color: #f6f3ec; */
  color: #6DF1B3;
}

#resume-btn {
  padding: 5px 10px;
  border-radius: 3px;
  background-color: #E9F4F6;
  color: #0A282E;
  border: none;
  position: relative; /* Ensure button position is relative for absolute positioning of pseudo-element */
  overflow: hidden; /* Hide any overflow of pseudo-element outside button */
  transition: transform 0.8s ease; /* Add transition for transform */
}

#resume-btn::before {
  content: '';
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  transition: transform 0.8s ease; /* Add transition for transform */
  z-index: -1; /* Place pseudo-element behind button content */
}

#resume-btn:hover::before {
  transform: translateY(-10px); /* Move pseudo-element up to create lifting effect */
}

#resume-btn:hover {
  transform: translateY(-5px); /* Move button content up slightly */
  box-shadow: 5px 7px #98ADB1; /* Larger box shadow on hover */
}

.element {
  margin: 7% 6% 7% 10%;
}

#name-header{
  padding: 0;
  margin: 0;
  font-size: 75px;
  color: #E9F4F6;
}

h1{
  color: #E9F4F6;
}

.container{
  display: flex;
  flex-direction: row;
  padding-top: 2%;
  padding-bottom: 2%;
}

button{
  border:none;
  cursor: pointer;
  background-color: inherit;
}

